.candidate-container {
  background-color: whitesmoke;
  margin: 0px 15px;
  color: darkslategrey;
  border-radius: 10px;
  padding: 20px;
}

.candidate-name {
  font-size: 36px;
  margin-bottom: 10px !important;
  padding-bottom: 0px;
}

.candidate-party {
  color: red;
  font-weight: bold;
}

.candidate-about {
  font-weight: 400;
}

.vote-btn {
  background-color: darkslategray;
  color: whitesmoke;
  width: 100%;
  border-radius: 5px;
  padding: 15px;
  margin-top: 30px;
  font-size: 24px;
}
