.auth-container {
  display: flex;
}

.auth-container .section-content {
  width: 70%;
  height: 100vh;
  display: flex;
  padding: 50px;
  padding-bottom: 0px;
  align-items: center;
}

.auth-container .section-content > form > input {
  width: 500px;
  padding: 20px;
  border-radius: 2px;
  border: none !important ;
  margin-bottom: 16px;
  font-weight: bold;
}
