.banner-container {
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;

  /* background-image: url("https://images.unsplash.com/photo-1619392553201-3d9ab3169271?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=2070&q=80");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat; */
}

.banner-container > div {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.banner-anim-container {
  border-radius: 100%;
  background-color: white;
  padding: 20px;
}

.banner-title {
  margin-bottom: 0;
  padding-bottom: 24px;
  font-size: 60px;
  font-weight: bold;
}

.banner-subtitle {
  margin-top: 0;
  padding-top: 0;
  font-size: 24px;
  font-weight: 400;
  color: grey;
  width: 800px;
  text-align: center;
  justify-self: center;
}

.votenow-btn {
  padding: 12px 24px;
  border-radius: 2px;
  border: none;
  cursor: pointer;
  margin-top: 20px;
  font-weight: bold;
  color: black;
}
